@import url('https://fonts.googleapis.com/css2?family=Kanit&display=swap');

body {
    background-color: #006d72;
    margin: 0;
    font-family: 'Kanit', sans-serif;
}

.main-content {
    min-height: 60vh;
    background: #006d72;
    background: linear-gradient(to right, #0d3b66, #00afb9);
    color: white;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100dvh;
}

.details {
    text-align: center;
    max-width: 100vw;
}

.contact-pic {
    height: 6rem;
    width: 6rem;
    border-radius: 50%;
    border: 2px solid #fff;
}

.location p {
    display: inline-block;
}

.location svg {
    vertical-align: middle;
}

.contact {
    margin-top: 20px;
    display: flex;
}

.contact .col-4 {
    width: 10rem;
    text-align: center;
}

.contact .col-4 img {
    width: 20%;
    margin-bottom: 0;
}

.contact .col-4 p a {
    margin-top: 0;
}

.heading {
    font-weight: 400;
    font-size: 1.3rem;
    margin: 1rem 0;
}
